body, h1, p {
  margin: 0;
  padding: 0;
}

body {
  background-color: #f0f0f0;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 900px;
  background-color: #fff;
  border-radius: 8px;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

.breed-select {
  width: 100%;
  margin-bottom: 20px;
  padding: 10px;
  font-size: 16px;
}

.loader {
  height: 32px;
  width: 150px;
  box-sizing: border-box;
  border: 2px solid #fff;
  border-radius: 20px;
  display: none;
  position: relative;
  overflow: hidden;
}

.loader:before {
  content: "";
  width: 24px;
  height: 24px;
  background: #ff3d00;
  border-radius: 50%;
  animation: ballbns 3s ease-in-out infinite;
  position: absolute;
  bottom: 2px;
  left: 0;
}

@keyframes ballbns {
  0%, 100% {
    left: 0;
    transform: translateX(0%);
    box-shadow: -5px 0 0 -1px rgba(255, 61, 0, .9), -10px 0 0 -2px rgba(255, 61, 0, .8), -15px 0 0 -4px rgba(255, 61, 0, .6), -20px 0 0 -6px rgba(255, 61, 0, .4), -25px 0 0 -8px rgba(255, 61, 0, .2);
  }

  49% {
    left: 100%;
    transform: translateX(-100%);
    box-shadow: -5px 0 0 -1px rgba(255, 61, 0, .9), -10px 0 0 -2px rgba(255, 61, 0, .8), -15px 0 0 -4px rgba(255, 61, 0, .6), -20px 0 0 -6px rgba(255, 61, 0, .4), -25px 0 0 -8px rgba(255, 61, 0, .2);
  }

  51% {
    left: 100%;
    transform: translateX(-100%);
    box-shadow: 5px 0 0 -1px rgba(255, 61, 0, .9), 10px 0 0 -2px rgba(255, 61, 0, .8), 15px 0 0 -4px rgba(255, 61, 0, .6), 20px 0 0 -6px rgba(255, 61, 0, .4), 25px 0 0 -8px rgba(255, 61, 0, .2);
  }
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.cat-info {
  flex-wrap: wrap;
  gap: 20px;
  display: flex;
}

.cat-info img {
  width: 50%;
  max-width: 100px;
  height: auto;
  border: 1px solid #000;
  margin-bottom: 10px;
}

.cat-info h2 {
  margin-bottom: 10px;
  font-size: 24px;
}

.cat-info p {
  width: 400px;
  font-size: 16px;
  line-height: 1.5;
}

.notiflix-notify-message {
  font-size: 16px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 4px;
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 16px;
  display: none;
}

.load-more:hover {
  background-color: #45a049;
}

